.tvshow-card {
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
	margin-bottom: 40px;
	text-decoration: none;
	color: #000000;
}

.tvshow-card:hover {
	box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
}

.tvshow-card__footer {
	margin: 10px 5px 10px 10px;
}

.tvshow-card__info {
	display: inline-block;
	margin-left: 15px;
}

.tvshow-card__title,
.tvshow-card__date {
	display: block;
}

.tvshow-card__title {
	font-size: 16px;
	font-weight: bold;
}

.tvshow-card__date {
	color: #666666;
	font-size: 14px;
}
